import logo from './logo.svg';
import './App.css';
import React from 'react';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      remainingSecs: 0,
      mins: "0",
      secs: "0",
      timerHandle: null
    };
    this.handleUpdate = this.handleUpdate.bind(this)
    this.handleGO = this.handleGO.bind(this)
    this.handleSTOP = this.handleSTOP.bind(this)
    this.decrement = this.decrement.bind(this)
  }

  handleUpdate(event) {
    const name = event.target.name;
    this.setState({
      [name]: event.target.value
    });
  }

  handleGO(event) {
    if (this.state.timerHandle) {
      clearInterval(this.state.timerHandle)
    }
    const mins = parseInt(this.state.mins)
    const secs = parseInt(this.state.secs)
    this.setState({remainingSecs: minuteSecs2TotalSecs(mins, secs)})
    this.setState({timerHandle: setInterval(this.decrement, 1000)})
  }

  handleSTOP() {
    if (this.state.timerHandle) {
      clearInterval(this.state.timerHandle)
      this.setState({timerHandle: null})
    } else {
      this.setState({timerHandle: setInterval(this.decrement, 1000)})
    }
  }

  decrement() {
    this.setState({
      remainingSecs: this.state.remainingSecs - 1
    });
    if (this.state.remainingSecs === 0) {
      clearInterval(this.state.timerHandle)
    }
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          {/* <img src={logo} className="App-logo" alt="logo" /> */}
          <p className="App-text">
            {BinaryDisplay(this.state.remainingSecs)}
          </p>
          <p className="App-inner">
            <input className="App-form" type="text" name="mins" value={this.state.mins} onChange={this.handleUpdate}/>
            :
            <input className="App-form" type="text" name="secs" value={this.state.secs} onChange={this.handleUpdate}/>
          </p>
          <p>
            <button className="App-button" onClick={this.handleGO}>GO!</button>
            <button className="App-button" onClick={this.handleSTOP}>STOP!</button>
          </p>
        </header>
      </div>
    );
  }
}

function BinaryDisplay(totalSecs) {
  var minSecs = totalSecs2MinuteSecs(totalSecs)
  var mins = minSecs[0]
  var secs = minSecs[1]
  return (
    <code>{dec2binary(mins)}.{dec2binary(secs)}</code>
  );
}

function totalSecs2MinuteSecs(totalSecs) {
  var mins = totalSecs / 60;
  mins = Math.floor(mins);
  var secs = totalSecs % 60;
  return [mins, secs];
}

function minuteSecs2TotalSecs(mins, secs) {
  return (mins * 60) + secs;
}

function dec2binary(number) {
  var binary = "";
  if (number >= 32) {
    binary += '1';
    number = number - 32;
  } else {
    binary += '0';
  }
  if (number >= 16) {
    binary += '1';
    number = number - 16;
  } else {
    binary += '0';
  }
  if (number >= 8) {
    binary += '1';
    number = number - 8;
  } else {
    binary += 0;
  }
  if (number >= 4) {
    binary += '1';
    number = number - 4;
  } else {
    binary += 0;
  }
  if (number >= 2) {
    binary += '1';
    number = number - 2;
  } else {
    binary += 0;
  }
  if (number >= 1) {
    binary += '1';
  } else {
    binary += 0;
  }
  return binary;
}

export default App;
